// start header
.header {
  width: 100%;
  height: 100px;
  position: relative;
  background-color: #21325e;
  // position: static !important;
 
  @media (max-width: 600px) {
    // position: fixed;
    z-index: 100;
  }
  @media (min-width: 601px){
  position: static !important;
 }

  // start container
  .container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;

    @media (max-width: 600px) {
      justify-content: flex-end;
    }

    .btn-nav {
      display: none;

      @media (max-width: 600px) {
        display: flex;
      }
    }

    .links {
      display: flex;
      align-items: center;
      height: 100%;

      @media (max-width: 600px) {
        display: none;
        position: absolute;
        font-size: 16px;
        flex-direction: column;
        top: 100px;
        background-color: #21325e;
        width: 100%;
        right: 0;
        z-index: 99;
        padding: 20px 0;
        height: calc(100vh - 100px);
        align-items: center;
        justify-content: center;
      }

      a {
        // height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        padding: 0 20px;
        font-size: 28px;

        @media (max-width: 600px) {
          padding: 20px 0;
        }
      }
    }

    #open {
      display: flex;
      // position: fixed;
    }
    // #close {
    //   display: none;
    // }
    //  end container
  }

  //  end header
}
