// import style header
@use "./styles/components/Header";

// import style about
@use "./styles/components/About";

// import style skills
@use "./styles/components/Skills";

// import styles Certificates
@use "./styles/pages/Certificates";

// import styles Projects
@use "./styles/components/Projects";

// import styles ArrowToUp
@use "./styles/components/ArrowToUp";

// import style footer
@use "./styles/components/Footer";

// import styles PageProject
@use "./styles/pages/PageProject";

// import style Error
@use "./styles/pages/Error";

/* start global rules */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "cairo", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  word-wrap: break-word;
  max-width: 100%;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

/* end global rules */
