.timeline {
  .container {
    .date {
      color: black;
      font-weight: bold;
    }
    .text {
      color: #fff;

      .subtitle {
        color: #bdbdbd;
        font-style: italic;
      }
    }
    .link {
      margin-top: 10px;
      width: fit-content;
      margin-left: auto;
      color: white;
      text-decoration: underline;
      font-style: italic;
    }
  }
}

