.page-project {
  padding: 100px 0;

  .container {
    text-align: center;
    color: #3e497a;

    .img {
      margin: 20px auto;
      width: 600px;
      max-width: 100%;

      img {
        box-shadow: 0 1px 18px -6px #000;
        border-radius: 8px;
      }
    }

    .text {
      width: 600px;
      max-width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      word-wrap: break-word;
      margin: 15px auto 30px;
    }
    .btns {
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 600px;
      max-width: 100%;

      @media (max-width: 600px) {
        flex-direction: column;
      }

      .btn {
        color: white;
        background-color: #3e497a;
        padding: 15px 20px;
        border-radius: 8px;
        box-shadow: 0 1px 18px -6px #000;

        @media (max-width: 600px) {
          width: 100%;

          &:not(:last-child) {
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}
