.projects {
  padding: 100px 0;

  .container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 20px;

    @media (max-width: 600px) {
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }

    .box {
      overflow: hidden;
      border-radius: 8px;
      box-shadow: 0 1px 18px -6px #000;
      cursor: pointer;
      transition: box-shadow 0.25s;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      &:hover {
        box-shadow: 0 4px 13px 2px #000;
      }
        .img {
          height: 100%;
          img {
            height: 100%;
          }
        }

      .text {
        padding: 15px;
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;

        h3 {
          max-width: 100%;
          word-wrap: break-word;
        }
      }
    }
  }
}
