.arrow-to-up {
  .icon {
    background-color: #3e497a;
    border-radius: 6px;
    padding: 15px 20px;
    color: white;
    font-size: 25px;
    cursor: pointer;
    position: fixed;
    z-index: 99;
    bottom: 50px;
    right: 40px;
    display: none;
    transition: 0.5s;

    @media (max-width: 600px) {
      font-size: 16px;
      right: 15px;
    }
  }
}
