.footer {
  height: 100px;
  width: 100%;
  color: white;
  background-color: #21325e;

  @media (max-width: 600px) {
    height: auto;
  }

  .container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 600px) {
      flex-direction: column;
    }

    .my-name {
      span {
        color: #e9d35b;
      }

      @media (max-width: 600px) {
        padding: 20px 0;
      }
    }

    .social-media {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;

      a {
        padding: 0 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        color: white;
        transition: color 0.5s;

        &:hover{
          color: #e9d35b;
        }

        @media (max-width: 600px) {
          padding: 10px 20px 20px;
        }

        svg {
          font-size: 40px;
        }
      }
    }
  }
}
