.skills {
  padding-top: 100px;
  padding-bottom: 100px;

  h1 {
    color: #3e497a;
    text-align: center;
    margin-bottom: 80px;
    font-size: 60px;
  }
  .container {
    ul {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      gap: 20px;

      li {
        text-align: center;
        padding: 20px 0;
        background-color: #21325e;
        color: white;
        border-radius: 8px;

        svg {
          font-size: 60px;
          @media (max-width: 600px) {
            font-size: 50px;
          }
        }

        p {
          margin-top: 20px;
          font-weight: bold;
        }
      }
    }
  }
}
