.about {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #21325e;
  color: #f0f0f0;
  .container {
    h2 {
      font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
      font-size: 80px;
      color: #e9d35b;

      @media (max-width: 600px) {
        font-size: 40px;
      }
    }
    .content {
      p {
        font-size: 30px;
        font-weight: bold;
        margin: 30px 30px 0;
        line-height: 1.7;

        @media (max-width: 600px) {
          font-size: 20px;
        }

        & + a {
          margin: 15px auto;
          background-color: #e9d35b;
          width: fit-content;
          padding: 15px 20px;
          border-radius: 7px;
          color: #21325e;
          font-weight: bold;
          font-size: 20px;
        }
      }

      span {
        display: block;
        margin: 20px auto 5px;
      }

      .socialMedia {
        //   start a
        display: flex;
        align-items: center;
        justify-content: center;
        a {
          color: white;
          margin: 0 20px;
          transition: color 0.5s;

          &:hover {
            color: #e9d35b;
          }

          @media (max-width: 600px) {
            margin: 0 10px;
          }

          svg {
            font-size: 60px;
            margin: 5px;
            // color: white;

            @media (max-width: 600px) {
              font-size: 30px;
            }
          }
        }
      }
    }
  }
}
